import React, { useState, useEffect } from 'react';
import taco from '../images/taco.png';
import burritos from '../images/burritos.png';
import quesadillas from '../images/quesadillas.png';
import ensalada from '../images/ensalada.png';
import burger from '../images/burger.png';
import limeade from '../images/limeade.png';
import aqua from '../images/aqua.png';
import Close from './Close'




function Menu(){
    const [count, setCount] = useState(0);
    const [newItem, setItem] = useState({
        title: '',
        price: ''
    });

    const update = (e) => {
        const menubutton = document.getElementsByClassName('menubutton');
        e.preventDefault();
        setCount(count +1);
        setItem({
            title: e.target.name,
            price: e.target.value
        });

        for (let i = 0; i < menubutton.length; i++) {
            if (e.target.name == "tacos") {
                menubutton[0].textContent = "Added";
            } else if (e.target.name == "burritos") {
                menubutton[1].textContent = "Added";
            } else if (e.target.name == "quesadillas") {
                menubutton[2].textContent = "Added";
            } else if (e.target.name == "ensalada") {
                menubutton[3].textContent = "Added";
            } else if (e.target.name == "burger") {
                menubutton[4].textContent = "Added";
            } else if (e.target.name == "limeade") {
                menubutton[5].textContent = "Added";
            } else if (e.target.name == "agua de jamaica") {
                menubutton[6].textContent = "Added";
            }
        } 
    }
    

    useEffect(() => {
        const cartDisplay = document.getElementsByClassName('circle')[0];
        const itemList = document.getElementsByClassName('ullist')[0];
        const food = document.createTextNode(newItem.title);
        const price = document.createTextNode(newItem.price);

        cartDisplay.textContent = count;

        if (newItem.title !== "") {
            const ulChild = document.createElement('ul');
            ulChild.classList = "cover";
            const liChildfood = document.createElement('li');
            const liChildqty = document.createElement('li');
            const liChildprice = document.createElement('li');
            liChildprice.classList.add("price")
            const liChildtotal = document.createElement('li');
            liChildtotal.classList.add("linetotal")
            const qty = document.createElement('input')
            qty.classList.add("qty")
            qty.setAttribute("name", "quantity")
            qty.setAttribute("value", 1)
             
            itemList.append(ulChild);
            ulChild.append(liChildfood, liChildqty, liChildprice, liChildtotal);
            liChildfood.append(food);
            liChildqty.append(qty);
            liChildprice.append(price);
            

            if ( document.getElementsByClassName('cover')[0] ) {
            const qtyn = document.getElementsByClassName('qty')[0].value
            const pricen = document.getElementsByClassName('price')[0].textContent
            let multipl = qtyn * pricen
            console.log(multipl)
            liChildtotal.append(multipl);
            multipl = 0
            console.log(multipl)
            } else if ( document.getElementsByClassName('cover')[1] ) {
                const qtyn = document.getElementsByClassName('qty')[1].value
                const pricen = document.getElementsByClassName('price')[1].textContent
                const multipl = qtyn * pricen
                console.log(multipl)
                liChildtotal.append(multipl);
                multipl = 0
                console.log(multipl)
                } else if ( document.getElementsByClassName('cover')[2] ) {
                    const qtyn = document.getElementsByClassName('qty')[2].value
                    const pricen = document.getElementsByClassName('price')[2].textContent
                    const multipl = qtyn * pricen
                    liChildtotal.append(multipl);
                    } else if ( document.getElementsByClassName('cover')[3] ) {
                        const qtyn = document.getElementsByClassName('qty')[3].value
                        const pricen = document.getElementsByClassName('price')[3].textContent
                        const multipl = qtyn * pricen
                        liChildtotal.append(multipl);
                        } else if ( document.getElementsByClassName('cover')[4] ) {
                            const qtyn = document.getElementsByClassName('qty')[4].value
                            const pricen = document.getElementsByClassName('price')[4].textContent
                            const multipl = qtyn * pricen
                            liChildtotal.append(multipl);
                            } else if ( document.getElementsByClassName('cover')[5] ) {
                                const qtyn = document.getElementsByClassName('qty')[5].value
                                const pricen = document.getElementsByClassName('price')[5].textContent
                                const multipl = qtyn * pricen
                                liChildtotal.append(multipl);
                                } else if ( document.getElementsByClassName('cover')[6] ) {
                                    const qtyn = document.getElementsByClassName('qty')[6].value
                                    const pricen = document.getElementsByClassName('price')[6].textContent
                                    const multipl = qtyn * pricen
                                    liChildtotal.append(multipl);
                                    }

            
        }
            
    setItem({
        title: '',
        price: ''
    })   
}, [count]);

    return(
        <div className="menublock">
            <Close />
            <>
            <div className="foods">{food[0].title} <span>&#8358;{food[0].price}</span></div>
            <div className="food-border">
            <img src={taco} style={{"marginTop": "50px", "marginBottom": "30px"}} alt="taco" width="60%"/>
            <p className="food-details">{food[0].details}</p>
            <button className="menubutton" value={food[0].price} name={food[0].title} onClick={update}>Add To Cart</button>
            </div>
            </>
            <>
            <div className="foods">{food[1].title} <span>&#8358;{food[1].price}</span></div>
            <img src={burritos} style={{"marginTop": "50px", "marginBottom": "30px"}} loading="lazy" alt="burritos" width="50%"/>
            <p className="food-details">{food[1].details}</p> 
            <button className="menubutton" value={food[1].price} name={food[1].title} onClick={update}>Add To Cart</button>
            </>
            <>
            <div className="foods">{food[2].title} <span>&#8358;{food[2].price}</span></div>
            <img src={quesadillas} style={{"marginTop": "50px", "marginBottom": "30px"}} loading="lazy" alt="quesadillas" width="50%"/>
            <p className="food-details">{food[2].details}</p> 
            <button className="menubutton" value={food[2].price} name={food[2].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[3].title} <span>&#8358;{food[3].price}</span></div>
            <img src={ensalada} loading="lazy" alt="ensalada" width="60%"/>
            <p className="food-details">{food[3].details}</p> 
            <button className="menubutton" value={food[3].price} name={food[3].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[4].title} <span>&#8358;{food[4].price}</span></div>
            <img src={burger} loading="lazy" alt="ensalada" width="60%"/>
            <p className="food-details">{food[4].details}</p> 
            <button className="menubutton" value={food[4].price} name={food[4].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[5].title} <span>&#8358;{food[5].price}</span></div>
            <img src={limeade} style={{"marginTop": "50px", "marginBottom": "50px"}} loading="lazy" alt="" width="40%"/>
            <p className="food-details">{food[5].details}</p> 
            <button className="menubutton" value={food[5].price} name={food[5].title} onClick={update}>Unavailable</button>
            </>
            <>
            <div className="foods">{food[6].title} <span>&#8358;{food[6].price}</span></div>
            <img src={aqua} style={{"marginTop": "50px", "marginBottom": "50px"}} loading="lazy" alt="" width="22%"/>
            <p className="food-details">{food[6].details}</p> 
            <button className="menubutton" value={food[6].price} name={food[6].title} onClick={update}>Unavailable</button>
            </>
        </div>
    )
}


const food = [{
    title: 'tacos',
    price: 2500,
    details: 'Your choice of three soft handmade flour tortillas with meat, salsas, and lettuce.'
},
{
    title: 'burritos',
    price: 3000,
    details: 'A large handmade flour tortilla with a choice of lime rice or Mexican red rice, beans, meat, salsa, and cheese.'
},
{
    title: 'quesadillas',
    price: 4000,
    details: 'A large handmade flour tortilla stuffed mozzarella cheese and meat. Served with shredded lettuce, salsa, and fresh guacamole.'
},
{
    title: 'ensalada',
    price: 3000,
    details: 'A large handmade flour tortilla, filled with beans, lime rice or Mexican red rice, meat and lettuce. Topped with salsa, crispy tortilla strips, cheese, and house dressing.'
},
{
    title: 'burger',
    price: 2000,
    details: 'Neque porro quisquam est qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit....'
},
{
    title: 'limeade',
    price: 1000,
    details: 'Lime flavoured drink made with freshly squeezed limes, water and sugar.'
},
{
    title: 'agua de jamaica',
    price: 1000,
    details: 'Fresh hibiscus juice made with pineapple, ginger and bananas.'
}]

export default Menu